/*
Note: any .css or .scss files included in the 'styles' directory
will be correctly compiled during `gulp serve` and `gulp`
 */

@import "src/material-design-lite";

html, body {
  font-family: 'Roboto', 'Helvetica', sans-serif;
  margin: 0;
  padding: 0;
}

// Scroll style
.scroll-x {
  overflow-x: auto;
}

*::-webkit-scrollbar {
  width: 0.4rem;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background: rgba(0,0,0,.28);
}

// User avatar
.user {
  display: flex;
  flex-direction: row;
  align-items: center;

  .avatar {
    width: 48px;
    height: 48px;
    margin-right: 8px;
  }

  .name {
    font-size: 20px;
  }
}

.business {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 16px;

  .avatar {
    width: 56px;
    height: 56px;
    margin-right: 8px;
  }

  .name {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

.mdl-layout__drawer .mdl-navigation__link .material-icons {
  margin-right: 16px;
}

// Card
.mdl-card--full-width {
  width: 100%;
}

.mdl-card__table {
  .mdl-data-table {
    border: none;
  }
}

// Table
.mdl-data-table--full-width {
  width: 100%;
  max-width: 100%;
}

.mdl-data-table--selectable tr {
  td:first-child, th:first-child {
    width: 1px;
  }
}